.feedback {
  width: 100%;
  background: #00b3ff;
  position: relative;
}

.feedback__back {
  position: absolute;
  display: block;
  width: 50%;
  top: 0;
  left: 0;
  height: 100%;
  background: linear-gradient(to top left, transparent 50%, rgba(4, 24, 39, 0.5) 50%);

  @include vp-1023 {
    display: none;
  }
}

.feedback__form-wrapper {
  display: flex;
  justify-content: center;
  padding: 170px 0 250px;
  align-items: center;

  @include vp-1023 {
    padding: 50px 0 125px;
  }

  @include vp-767 {
    padding-top: 47px;
    padding-bottom: 88px;
  }
}

.feedback__image {
  width: custom-width(510, 1840);
  max-width: 510px;
  z-index: 2;

  @include vp-1023 {
    display: none;
  }
}

.feedback__form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 18px custom-width(30, 580);
  margin-left: custom-width(115, 1840);
  max-width: 580px;
  width: custom-width(580, 1840);

  @include vp-1023 {
    min-width: 670px;
    width: custom-width(670, 720);
    margin: 0;
    gap: 10px custom-width(30, 670);
  }

  @include vp-767 {
    min-width: 270px;
    width: custom-width(270, 288);
    grid-template-columns: 1fr;
    gap: 7px;
  }
}

.feedback__title {
  grid-column: 1/3;
  font-weight: 400;
  font-size: 36px;
  line-height: 41px;
  color: #ffffff;
  margin: 0;
  margin-left: -5px;
  font-family: $roboto;

  @include vp-1023 {
    margin: 0;
  }

  @include vp-767 {
    grid-column: 1/2;
    font-size: 30px;
    line-height: 42px;
  }
}

.feedback__text {
  grid-column: 1/3;
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  font-family: $pt-mono;
  margin-bottom: 17px;

  @include vp-767 {
    grid-column: 1/2;
    font-size: 14px;
    line-height: 19px;
    margin-bottom: 27px;
  }
}

.feedback input {
  background: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  border: none;
  padding: 14px;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: $pt-mono;
  color: #ffffff;

  @include vp-1023 {
    margin-bottom: 10px;
  }

  @include vp-767 {
    margin-bottom: 13px;
  }
}

.feedback input::placeholder {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: $pt-mono;
  color: #ffffff;
}

.feedback textarea {
  grid-column: 1/3;
  background: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  border: none;
  padding: 25px 15px;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: $pt-mono;
  color: #ffffff;
  margin-bottom: 13px;

  @include vp-1023 {
    padding-bottom: 15px;
    padding-top: 30px;
    margin-bottom: 25px;
  }

  @include vp-767 {
    grid-column: 1/2;
    padding-top: 23px;
    padding-bottom: 47px;
    margin-bottom: 23px;
  }
}

.feedback textarea::placeholder {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: $pt-mono;
  color: #ffffff;
}

.feedback button {
  background: #041427;
  border-radius: 3px;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  padding: 13px;
  font-family: $roboto;
  border: none;
  cursor: pointer;
  margin-right: 11px;

  @include vp-1023 {
    width: custom-width(265, 320);
  }

  @include vp-767 {
    width: 100%;
    margin-right: 0;
  }
}

.feedback__agreed-text {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.8;
  font-family: $pt-mono;
  align-self: center;
  cursor: pointer;
  position: relative;
  margin-left: 34px;
  margin-top: 14px;

  @include vp-1023 {
    margin-left: 26px;
    margin-top: 7px;
  }

  @include vp-767 {
    margin-left: 32px;
    margin-top: 18px;
  }
}

.feedback input[type="checkbox"] {
  display: none;
}

.feedback__checkbox-icon {
  position: absolute;
  top: 25%;
  left: -26px;

  &--hide {
    display: none;
  }
}
